.gridder-list {
  cursor: pointer;
  border: 1px solid #eee;
}

.gridder-list:hover {
  background: #f7f7f7;
  border: 1px solid #c5c5c5;
}

.gridder-list.active {
  background-color: #d3d4d4;
}

.gridder-show {
  border: 1px solid #eee;
  padding: 15px;
}

.gridder-navigation {
  float: right;
}

.gridder-navigation a {
  cursor: pointer;
  border: 1px solid #eee;
  margin-left: 5px;
  padding: 5px;
  display: inline-block;
}

.gridder-navigation a:hover {
  background: #eee;
}

